@charset "UTF-8";
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Roboto Medium"), url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Roboto Condensed"), url("../fonts/roboto_condensed-regular.woff2") format("woff2"), url("../fonts/roboto_condensed-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: local("Roboto Condensed Bold"), url("../fonts/roboto_condensed-bold.woff2") format("woff2"), url("../fonts/roboto_condensed-bold.woff") format("woff"); }

@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("PT Mono"), url("../fonts/pt_mono-regular.woff2") format("woff2"), url("../fonts/pt_mono-regular.woff") format("woff"); }

/*Цвета*/
a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  height: 100%;
  scroll-behavior: smooth; }

.page-body {
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  margin: 0 auto;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  scroll-behavior: smooth; }

.page-header {
  background-color: #1b2d37; }

.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  max-width: 1420px;
  margin: 0 auto;
  padding: 32px 10px 0; }
  @media (max-width: 1023px) {
    .page-header__wrapper {
      max-width: 100%;
      padding: 21px 24px 0; } }
  @media (max-width: 767px) {
    .page-header__wrapper {
      padding: 17px 16px 0; } }

.logo {
  display: flex;
  max-width: 250px; }

.logo__img img {
  display: block; }
  @media (max-width: 767px) {
    .logo__img img {
      width: 38px;
      height: 38px; } }

.logo__text {
  margin-left: 16px; }
  @media (max-width: 767px) {
    .logo__text {
      display: none; } }

.logo__text h2 {
  margin: 0;
  padding-top: 6px;
  font-weight: bold;
  font-size: 25px;
  line-height: 29px; }

.logo__text p {
  margin: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: #788891; }

.user-nav {
  display: flex;
  flex-wrap: wrap;
  align-items: center; }

.user-nav__link {
  display: flex;
  align-items: center;
  margin-left: 38px;
  margin-right: 50px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  color: #ffffff; }
  .user-nav__link:hover {
    color: #00b2ff; }
  .user-nav__link:active {
    opacity: 0.5; }
  @media (max-width: 1023px) {
    .user-nav__link {
      margin: 0; } }

@media (max-width: 1023px) {
  .user-nav__link--mail svg {
    width: 25px;
    height: 19px; } }

@media (max-width: 1023px) {
  .user-nav__link--tel svg {
    width: 24px;
    height: 24px; } }

.user-nav__link:not(:last-of-type) svg {
  margin-right: 20px; }
  @media (max-width: 1023px) {
    .user-nav__link:not(:last-of-type) svg {
      margin-right: 33px; } }
  @media (max-width: 767px) {
    .user-nav__link:not(:last-of-type) svg {
      margin-right: 23px; } }

.user-nav__link:last-of-type svg {
  margin-right: 16px; }
  @media (max-width: 1023px) {
    .user-nav__link:last-of-type svg {
      margin-right: 0; } }

@media (max-width: 1023px) {
  .user-nav__link span {
    display: none; } }

.user-nav__button {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
  padding: 14px 25px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 5px;
  cursor: pointer; }
  .user-nav__button:hover {
    background-color: #00b2ff; }
  .user-nav__button:active {
    opacity: 0.5; }
  @media (min-width: 1024px) and (max-width: 1420px) {
    .user-nav__button {
      margin-right: 0; } }
  @media (max-width: 1023px) {
    .user-nav__button {
      display: none; } }

.promo {
  display: flex;
  background-color: #1b2d37; }

.promo__text h2 {
  margin: 0;
  margin-bottom: 31px;
  font-size: 80px;
  line-height: 94px;
  font-weight: bold;
  letter-spacing: 0.07em;
  text-transform: uppercase; }

.promo__text p {
  margin: 0;
  margin-bottom: 37px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-weight: normal;
  font-size: 22px;
  line-height: 34px; }

.promo__button {
  padding: 17px 28px;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #00b2ff;
  border-radius: 5px; }

.promo__scroll {
  font-family: "PT Mono", "Arial", sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.5; }

.promo img {
  display: block; }
